export interface IAuth {
    // 认证姓名
    name?: string;

    // 身份证编号
    certNo?: string;

    // 认证状态 0 待认证(继续认证，上传了身份证，已经获取了bizNo 与 url) 1 认证失败 2 认证成功 3 从未认证 4 认证中 5 继续认证（上传了身份证，未获取bizNo 与 url）
    statusCode?: number;

    // 认证url
    url?: string;

    // 认证编号
    bizNo?: string;
}

export const AUTH_STATUS = {
    READY: 1, // 已经上传照片，有name certNo, 有url 与 bizNo 可以直接跳入确认界面
    FAIL: 2, // 认证失败
    SUCCESS: 3, // 认证成功
    NEVER: -1, // 从未认证
    AUTHING: 4, // 正在认证
    NEXT: 0 // 已经上传照片，有name  certNo, 没有url 与 bizNo，先获取再跳入确认界面
};



