import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';
import { IUserInterface } from '~/entity/user';
import { UPLOAD_ALBUM_URL, UPLOAD_ID_CARD, ROOT_PATH } from '~/config';
import { ToastMsg } from '~/entity/toast';
import { IAuth, AUTH_STATUS } from '~/entity/auth';
import Api from '~/api';
import { IConfirmAuthParams } from '~/entity/route_params';

/* 裁剪插件 */
