@Component({
    components: {
        'my-croppa': Croppa
    }
})
export default class AuthIndexComp extends Vue {

    isUploading = false;

    resInfo = '';

    @Action('showToast')
    showToast;

    // btnTxt: string = '上传身份证';

    @State(state => state.user.authentication)
    authentication: number;

    // 认证姓名
    @State(state => state.user.auth.name)
    authname: string;

    // 身份证号
    @State(state => state.user.auth.certNo)
    certNo: string;   
    
    // @State(state => state.user.user)
    // userInfo: IUserInterface;

    @State(state => state.user.auth.statusCode)
    authStatus: number;

    @State(state => state.user.auth.url)
    authUrl: string;

    @State(state => state.user.auth.bizNo)
    bizNo: string;

    @Action('updateAuthStatus')
    updateAuthStatus;

    /* 裁剪 */
    @Action('showLoading')
    showLoading;

    @Action('hideLoading')
    hideLoading;

    @Action('getUserRoles')
    getUserRoles;

    @Action('getMyInfo')
    getMyInfo;

    isCroping = false;

    myCroppaOption: ICroppaOption = {
        fullScreen: true,
        fromCamera: true,
        compressionRate: 0.7,
        quality: 6
    };

    $refs: {
        croppa: any
    };

    get statusTxt() {
        switch (this.authStatus) {
        case AUTH_STATUS.SUCCESS:
            return '认证成功';
        case AUTH_STATUS.FAIL:
            return '认证失败！';
        case AUTH_STATUS.AUTHING:
            return '认证中';
        default:
            return '';
        }
    }

    get upBtnTxt() {
        switch (this.authStatus) {
        case AUTH_STATUS.READY:
            return '继续认证';
        case AUTH_STATUS.FAIL:
            return '重新认证';
        case AUTH_STATUS.SUCCESS:
            return '';
        case AUTH_STATUS.NEVER:
            return '';
        case AUTH_STATUS.AUTHING:
            return '';
        case AUTH_STATUS.NEXT:
            return '继续认证';
        default:
            return '';
        }
    }

    get downBtnTxt() {
        switch (this.authStatus) {
        case AUTH_STATUS.READY:
            return '重新上传';
        case AUTH_STATUS.FAIL:
            return '重新上传';
        case AUTH_STATUS.SUCCESS:
            return '';
        case AUTH_STATUS.NEVER:
            return '上传照片';
        case AUTH_STATUS.AUTHING:
            return '';
        case AUTH_STATUS.NEXT:
            return '重新上传';
        default:
            return '';
        }
    }

    async activated() {
        this.isUploading = false;
        this.isCroping = false;
        // 在这个地方重新查询一次用户状态
        await Utils.initedToken();
        this.getMyInfo();
        this.getUserRoles();
    }
    
    deactivated() {
        if (this.isUploading) {
            this['destoried']();
        }
        if (this.isCroping) {
            this.hideCroppa();
        }
    }

    async clickUpBtn() {
        try {
              // 检查是否有url 与 bizNo
            if (this.authStatus === AUTH_STATUS.NEXT || this.authStatus === AUTH_STATUS.FAIL) {
                const res = await Api.authenticate();
                const url = res.body.obj.url;
                const bizNo = res.body.obj.bizNo;
                const auth: IAuth = {
                    url,
                    bizNo,
                    statusCode: AUTH_STATUS.READY
                };
                this.updateAuthStatus(auth);
            } 

            const params: IConfirmAuthParams = {
                url: this.authUrl,
                bizNo: this.bizNo
            };
            // 清空照片上传的错误提示
            this.resInfo = '';
            // 在这里发出同步请求，打开静态页面
            const url = ROOT_PATH + '/zhima/auth.html?url=' + encodeURIComponent(this.authUrl);
            console.log('静态页面： ' + url);
            window.location.href = url;

            // this['$router'].push({
            //     name: 'confirmAuth',
            //     params
            // });
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }

    // uploadPhoto(event) {
    //     // 上传照片
    //     this.isUploading = true;
    //     let image = document.getElementById('auth-img'); // 设置image
    //     this['clip'](event , { // 设置clip
    //         resultObj : image,
    //         headers: {
    //             'access-token': this['$store'].state.token
    //         },
    //         hintTxt: '图片识别中',
    //         aspectRatio: NaN,
    //         disableOrientation: true,
	// 		// aspectRatio : 'Free', // 裁剪比例
    //         uploadServer: UPLOAD_ID_CARD,
    //         filed: 'idcard',
    //         uploadSuccess(res) {
    //             console.log('uploadSuccess');
    //             console.log(res);

    //             this.showToast(new ToastMsg('识别身份证成功'));
    //             this.resInfo = '若信息有误请重新上传身份证图片';

    //             // 在这里接受到 身份证 与 姓名 更新状态
    //             const auth: IAuth = {
    //                 statusCode: AUTH_STATUS.NEXT,
    //                 certNo: res.obj.certNo,
    //                 name: res.obj.name
    //             };

    //             this.updateAuthStatus(auth);
                
    //             this.isUploading = false;
    //         },
    //         uploadError(res) {
    //             console.log('uploadError');   
    //             let errorTxt = '上传失败';
    //             if (res && res.msgText) {
    //                 errorTxt = res.msgText;
    //             }
    //             this.showToast(new ToastMsg(errorTxt));
    //             this.isUploading = false;
    //             this.resInfo = res.msgText;
    //         }
    //     });
    // }

    /************* 自定义裁剪上传 ***************/
    selectPhoto() {
        this.isCroping = true;
        this.$refs.croppa.chooseFile();
    }

    confirmCroppa(data) {
         // 在这里上传头像
         this.showLoading();

         const url = UPLOAD_ID_CARD;
         const headers = {
             'access-token': this['$store'].state.token
         };
         const params: IImgXhrParams = {
             base64Code: data.base64Code,
             type: data.type,
             filename: data.filename,
             filed: 'idcard'
         };
 
        xhr('POST', url, headers, params, async res => {
            try {
                const msgStatus = res.msgStatus; 
                if (msgStatus === 0) {
                    this.showToast(new ToastMsg('识别身份证成功'));
                    this.resInfo = '若信息有误请重新上传身份证图片';
    
                    // 在这里接受到 身份证 与 姓名 更新状态
                    const auth: IAuth = {
                        statusCode: AUTH_STATUS.NEXT,
                        certNo: res.obj.certNo,
                        name: res.obj.name
                    };
    
                    this.updateAuthStatus(auth);
                } else {
                    this.resInfo = res.msgText;
                    throw new Error(res.msgText);
                }
            } catch (e) {
                this.showToast(new ToastMsg(e.message));
            } finally {
                this.hideLoading();
                this.hideCroppa();
            }
        }, () => {
            this.showToast(new ToastMsg('上传失败'));
            this.hideLoading();
            this.hideCroppa();
        }, true, true);
    }

    cancelCroppa() {
        this.hideCroppa();
    }

    hideCroppa() {
        this.$refs.croppa.hide();
        this.isCroping = false;
    }
}



